<template>
  <div class="page">
    <div class="detail">
      <div class="left">
        <el-image class="photo" :src="detail.photo"></el-image>
      </div>
      <div class="right">
        <div class="row">参赛者姓名：{{detail.name}}</div>
        <div class="row">参赛编号：{{detail.serial}}</div>
        <div class="row">就读学校：{{detail.school}}</div>
        <div class="row">就读年级：{{detail.grade}}</div>
        <div class="row">参加组别：{{detail.group}}</div>
        <div class="row">联系电话：{{detail.phone}}</div>
        <div class="row">评委评分：{{detail.rate}}</div>
        <div class="row">参赛状态：{{detail.statu}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      detail: {
        // 姓名
        name: '我是姓名',
        // 照片
        photo:
          'https://gss0.bdstatic.com/94o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike150%2C5%2C5%2C150%2C50/sign=9d5157f3a251f3ded7bfb136f5879b7a/4034970a304e251f958d7bc1aa86c9177f3e5394.jpg',
        // 编号
        serial: '我是编号',
        // 就读学校
        school: '皇冠中学',
        // 就读年级
        grade: '初三',
        // 参赛组别
        group: '组别',
        // 联系电话
        phone: '18580529212',
        // 评委评分
        rate: '9.5',
        // 参赛状态
        statu: '已晋级'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.page {
  width: 100vw;
  height: 105vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vw;
  background-image: url('../../assets/img/botp.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  .detail {
    width: 100%;
    padding: 4vw;
    background-color: #ffffff;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    .left {
      width: 40%;
      .photo {
        width: 100%;
        display: block;
      }
    }
    .right {
      width: 55%;
      height: 170px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .row {
        width: 100%;
        color: #222;
        font-size: 14px;
      }
    }
  }
}
</style>



